@import url(https://fonts.googleapis.com/css2?family=Alex+Brush&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ruda:wght@600&display=swap);
.container {
  background-image: url(/static/media/fondo.9495546b.png);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 1000px;
  height: 100%;
  text-align: center;
  padding: 20px 10px;
}

#inputForm{
    margin-right: 20px;
    min-width: 180px;
}
 .input-button {
      margin-right: 50px;
    }
input{
    min-width: 130px;
  
}
.ui.grid>.row>.column{
    padding: 0px;
}


.ui.red.button{
    margin: 0px 20px;
}

h2.ui.header{
    font-family: 'Ruda', sans-serif;
    text-align: left;
}


.butons{
    display: flex;
}
#completedTodo{
    text-decoration: line-through;
    color: gray
}

#flower-list{
    color: #831AEE;
    font-size: 35px;
    margin-bottom:-8px;
    margin-right: 10px;
}

.prueba{
    
    align-items: center;
}

@media only screen and (max-width: 700px) {
	    
   
        h2.ui.header{
            font-size: 20px;
        }

        .ui.grid>.row{
            padding: 10px 0px;
            justify-content: space-around;
        }

        .ui.red.button{
            margin: 0px 10px;
        }

        
    }
.nav{
    display: flex;
    justify-content:space-between;
    font-size: 35px;
    background-image:url(/static/media/fondo.9495546b.png);
    position: fixed;
    top: 0;
    right:0;
    z-index: 2;
    width: 100%;
    padding: 10px;
   
}

.navbar h1{
    font-family: 'Alex Brush', cursive;
    font-size: 40px;
}

.icons svg{
    margin: 0px 10px;
    cursor: pointer;
}

.navbar img{
    max-width: 350px;
    width:100%
}

.navbar p{
    font-size: 26px;
    font-family: 'Ruda', sans-serif;
   
}

@media only screen and (max-width: 700px) {
	    
    .nav{
        display: flex;
        justify-content:space-between;
        font-size: 35px;
       

    }.navbar h1{
        font-size: 35px;
    }

    .navbar p{
        font-size:18px;
               
    }
    .navbar img{
        max-width: 300px;
        width:100%
    }
	}
    @media only screen and (max-width: 400px) {
	    
        .nav{
            display: flex;
            justify-content:space-between;
            font-size: 26px;
           
    
        }.navbar h1{
            font-size: 26px;
        }
    
        .navbar p{
            font-size:16px;
                   
        }
        .navbar img{
            max-width: 200px;
            width:100%
        }
        }
.modal {
  position: fixed;
  background-color: rgba(32, 35, 41, 0.8);
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
}

.containerModal {
  height: 30rem;
  width: 80%;
  background-image: url(/static/media/fondom.cbf8f7d7.jpg);
  border-radius: 5px;
  position: relative;
  overflow-y: scroll;
  overflow-x: hidden;
}
.containerTitle {
  font-family: "Ruda", sans-serif;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
}
#title {
  height: 20%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url(/static/media/fondom.cbf8f7d7.jpg);
  border-radius: 5px 5px 0 0;
  font-size: 2rem;
  margin: 0;
  box-shadow: 0 1px 5px #6f6f6f;
}

.close {
  top: -5px;
  right: -10px;
  border: none;
  background: none;
  color: #000000;
  font-size: 3rem;
  position: absolute;
  cursor: pointer;
}

.iconColor-deleted {
  color: #d80000;
  margin-bottom: -4px;
}

.iconColor-completed {
  color: #0fa958;
  margin-bottom: -4px;
}

.modalFil {
  text-align: center;
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modalFilter p {
  text-align: left;
  align-items: center;
}

